/*jshint esversion: 6 */
import React, { Component } from "react";
import * as Styled from "./styled/SearchPlus.js";
/* redux */
import { connect } from "react-redux";
import * as actions from "data/actions";
/* component */
class SearchPlus extends Component {
	constructor() {
		super();
		this.state = {
			selectedOption: { value: 1 }
		};
	}
	handleChange = selectedOption => {
		this.setState({ selectedOption });
	};
	render() {
		const options = [
			{
				value: 100,
				label: (
					<span>
						<span className="icon-ui-thumbs-up" />
						<span>100</span>
					</span>
				)
			},
			{
				value: 10,
				label: (
					<span>
						<span className="icon-ui-thumbs-up" />
						<span>10</span>
					</span>
				)
			},
			{
				value: 5,
				label: (
					<span>
						<span className="icon-ui-thumbs-up" />
						<span>5</span>
					</span>
				)
			},
			{
				value: 1,
				label: (
					<span>
						<span className="icon-ui-thumbs-up" />
						<span>1</span>
					</span>
				)
			},
			{
				value: -1,
				label: (
					<span>
						<span className="icon-ui-thumbs-down" />
						<span>-1</span>
					</span>
				)
			},
			{
				value: -5,
				label: (
					<span>
						<span className="icon-ui-thumbs-down" />
						<span>-5</span>
					</span>
				)
			},
			{
				value: -10,
				label: (
					<span>
						<span className="icon-ui-thumbs-down" />
						<span>-10</span>
					</span>
				)
			},
			{
				value: -100,
				label: (
					<span>
						<span className="icon-ui-thumbs-down" />
						<span>-100</span>
					</span>
				)
			}
		];
		const { selectedOption } = this.state;
		const value = selectedOption && selectedOption.value;
		const renderOptions = function() {};

		return (
			<Styled.SearchPlus>
				<select>{renderOptions()}</select>
			</Styled.SearchPlus>
		);
	}
}
const mapStateToProps = (state, ownProps) => {
	return {
		filters: state.filters
	};
};
const mapDispatchToProps = (dispatch, ownProps) => ({
	dispatch_filterRemove: filter => {
		dispatch(actions.filterRemove(filter));
	}
});
const ConnectedSearchPlus = connect(mapStateToProps, mapDispatchToProps)(SearchPlus);

/*
	Components
*/
export default ConnectedSearchPlus;



